<template>
	<div class=" fundManage_body">
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%" max-height='350'>
					<!-- 循环不需要改变的 -->
					<el-table-column v-for="(tableHeaderitem,index) of tableHeader " sortable  :key="index" :prop="tableHeaderitem.param" :label="tableHeaderitem.text" :width='tableHeaderitem.width'>
					</el-table-column>
					<!-- 需要编辑的 -->
					<el-table-column sortable prop="valuetion_invest" label="投资时估值(亿元)" width="180">
						<template slot-scope="scope">
							<div class="" v-if="scope.$index === edit_index">
								<el-input placeholder="请输入内容" v-model="scope.row.valuetion_invest" clearable>
								</el-input>
							</div>
							<div class="" v-else>
								{{ scope.row.valuetion_invest }}
							</div>
						</template>
					</el-table-column>
					<!-- <el-table-column sortable prop="protocol_end_date" label="协议到期日" width="180">
						<template slot-scope="scope">
							<div class="">
								{{ scope.row.protocol_end_date }}
							</div>
						</template>
					</el-table-column> -->
					<el-table-column sortable prop="share_rate" label="股权占比" width="140">
						<template slot-scope="scope">
							<div class="flex_between" v-if="scope.$index === edit_index">
								<el-input placeholder="请输入内容" v-model="scope.row.share_rate" clearable>
								</el-input>
								%
							</div>
							<div class="" v-else>
								{{ scope.row.share_rate }}%
							</div>
						</template>
					</el-table-column>
					<el-table-column sortable prop="latest_valutation" label="最新估值(亿元)" width="150">
						<template slot-scope="scope">
							<div class="" v-if="scope.$index === edit_index">
								<el-input placeholder="请输入内容" v-model="scope.row.latest_valutation" clearable>
								</el-input>
							</div>
							<div class="" v-else>
								{{ scope.row.latest_valutation }}
							</div>
						</template>
					</el-table-column>
					<el-table-column sortable prop="check_rate" label="复核倍数" width="120">
						<template slot-scope="scope">
							<!-- <div class="" v-if="scope.$index === edit_index">
								<el-input placeholder="请输入内容" v-model="scope.row.check_rate" clearable>
								</el-input>
							</div> -->
							<div class="" >
								{{ scope.row.check_rate }}
							</div>
						</template>
					</el-table-column>
					<el-table-column sortable prop="times_update_time" label="更新时间" width="180">
						<template slot-scope="scope">
							<!-- <div class="" v-if="scope.$index === edit_index">
								<el-date-picker v-model="scope.row.times_update_time" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" clearable>
    							</el-date-picker>
							</div> -->
							<div class="" >
								{{ scope.row.times_update_time }}
							</div>
						</template>
					</el-table-column>

					<!-- 操作栏 -->
					<el-table-column fixed="right" label="操作" width="160">
						<template slot-scope="scope">
							<el-button v-if="scope.$index !== edit_index" type="text" size="small" @click="toClick(scope.row)">查看</el-button>
							<el-button v-if="scope.$index !== edit_index" type="text" size="small" @click="toEditor(scope)">编辑</el-button>
                            <el-button v-if="scope.$index !== edit_index" type="text" size="small" @click="deleteFund(scope.row)">删除</el-button>

							<el-button v-if="scope.$index === edit_index" type="text" size="small" @click="editSave(scope.row)">保存</el-button>
							<el-button v-if="scope.$index === edit_index" type="text" size="small" @click="editCancel(scope)">取消</el-button>
							
						</template>
					</el-table-column>
					<!-- 空值时的显示状态 -->
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<!-- <el-pagination prev-text="上一页" next-text="下一页" background layout="prev, pager, next" :total="1000">
				</el-pagination> -->
				<!-- <el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :total="total" :current-page="page" :page-size="pageSize"  layout="sizes, prev, pager, next, jumper" >
    			</el-pagination> -->
			</div>
		</div>
		
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import {directGetSubscriptionFund,directSaveSubscriptionFund,directDelSubscriptionFund} from '../../config/apiSj'
	
	export default {
		data() {
			return {
				state1: '',
				tableIndex: -1,
				multipleSelection: [],
				isSetPowerDialogShow: false,
				selectedData: [],
				tableHeader:[
					{
						param:'id',
						text:'序号',
						width:'80',
					},{
						param:'short_name',
						text:'认购基金简称',
						width:'150',
					},{
						param:'sub_fund_type',
						text:'认购基金类别',
						width:'150',
					},{
						param:'name',
						text:'认购基金全称',
						width:'180',
					},{
						param:'subscribe',
						text:'认缴(万元)',
						width:'120',
					},{
						param:'paied',
						text:'实缴(万元)',
						width:'120',
					},{
						param:'paied_rate',
						text:'实缴进度',
						width:'120',
					},{
						param:'fair_value',
						text:'公允价值(万元)',
						width:'150',
					},{
						param:'times',
						text:'倍数',
						width:'120',
					},{
						param:'xirr',
						text:'XIRR',
						width:'120',
					},{
						param:'return_capital',
						text:'返本(万元)',
						width:'150',
					},{
						param:'profit',
						text:'收益(万元)',
						width:'120',
					},{
						param:'quit_date',
						text:'最近退出日期',
						width:'150',
					},{
						param:'quit_status',
						text:'退出状态',
						width:'120',
					},{
						param:'unitValue',
						text:'币种',
						width:'120',
					},
				],
				tableData: [
				],
				
				//编辑时的数据和id
				edit_index:'',
				edit_id:'',
				editorData:{},

				//分页参数
				total:null,
				page:1,
				pageSize:10,
				
				unit:'',
				id:'',
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.unit = decodeURIComponent(this.$route.query.unit);
			this.getListData();
		},
		mounted() {},
		components: {},
		methods: {

			//--------获取数据-----------
			getListData(){
				directGetSubscriptionFund({
					direct_invest_id:this.id,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data  = res.data.payload;
						// console.log(data);
						data.forEach((element,index) => {
							if(element.sub_fund_type==='1'){
								element.sub_fund_type = '母基金'
							}else{
								element.sub_fund_type = '自管基金'
							}
							element.quit_date = '-';
							element.share_rate = parseFloat(element.share_rate)*100;
							
							if(Math.floor(element.share_rate)!=element.share_rate){
								element.share_rate = element.share_rate.toFixed(4);
							}
							element.unitValue = this.unit;
						});
						this.tableData = data;
					}
				})
			},

			//--------操作-----------
			deleteFund(row){
				let data = JSON.parse(JSON.stringify(row));
				if(data.sub_fund_type=='母基金'){
					data.sub_fund_type = 1
				}else{
					data.sub_fund_type = 2
				}
				dialogConfirmHasbtn('确定要删除该认购基金吗?').then(() => {
					directDelSubscriptionFund({
						sub_fund_type:data.sub_fund_type,
						c_id:data.c_id,
					}).then(res => {
						if (res.data.msgNo == 1) {
							dialogConfirm("success", '删除成功');
							this.getListData();
						}
					})
				}).catch(() => {});
			},
			toClick(row){
				let data = JSON.parse(JSON.stringify(row));
				let viewName = '';
				if(data.sub_fund_type=='母基金'){
					data.sub_fund_type = 1
				}else{
					data.sub_fund_type = 2
				}
				if(data.sub_fund_type==1){
					viewName = 'motherFundCompanyDetail'
				}else{
					viewName = 'selfAdministerFundCompanyDetail'
				}
				this.$router.push({
					name:viewName,
					query:{
						id:data.p_id,
					}
					
				})
			},
			toEditor(scope){
				console.log(scope);
				if(this.edit_id){
					this.tableData[this.edit_index] = JSON.parse(JSON.stringify(this.editorData));
					this.edit_id = scope.row.c_id;//存储的id
					this.edit_index = scope.$index;//存储对应的index
					this.editorData = scope.row;//copy的数据
				}else{
					this.edit_id = scope.row.c_id;//存储的id
					this.edit_index = scope.$index;//存储对应的index
					this.editorData = JSON.parse(JSON.stringify(scope.row));//copy的数据
				}
			},
			editCancel(scope){
				this.tableData[scope.$index] = JSON.parse(JSON.stringify(this.editorData));
				this.edit_id = '';//存储的id
				this.edit_index = '';//存储对应的index
				this.editorData = {};//copy的数据
			},
			editSave(row){
				let data = JSON.parse(JSON.stringify(row));
				if(data.sub_fund_type=='母基金'){
					data.sub_fund_type = 1
				}else{
					data.sub_fund_type = 2
				}
				console.log(Number(data.share_rate));
				console.log(data.share_rate);
				if (  Number(data.share_rate) != data.share_rate) {
					dialogConfirm("info", '股权占比录入错误');
					return
				} 
				if (Number(data.share_rate) > 100 ) {
					dialogConfirm("info", '股权占比不能大于100');
					return
				}
				if ( Number(data.share_rate) < 0 ) {
					dialogConfirm("info", '股权占比不能小于0');
					return
				} 
				directSaveSubscriptionFund({
					sub_fund_type:data.sub_fund_type,
					c_id:data.c_id,
					latest_valutation:data.latest_valutation,
					share_rate:data.share_rate/100,
					check_rate:data.check_rate,
					valuetion_invest:data.valuetion_invest
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data  = res.data.payload;

						this.edit_id = '';//存储的id
						this.edit_index = '';//存储对应的index
						this.editorData = {};//copy的数据

						this.getListData();
						dialogConfirm('success','编辑成功');
					}
				})
			},
			

			

			//--------分页-----------
			//修改页面条数时调用pageSize
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getListData()
			},
			//修该当前页数时调用currentPage
			changeCurrentPage(e){
				this.page = e;
				this.getListData()
			},
			
		},
	}
</script>

<style scoped="scoped">
	/* .body {
		padding: 0 30px 30px 30px;
	} */
	/*公司名称模块开始*/
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	/*公司名称模块结束*/
	
	.under_fund_header {
		margin-top: 19px;
		margin-bottom: 6px;
		color: #6292F4;
		font-size: 12px;
	}
	
	.under_fund_header span {
		color: #6292F4;
		padding: 0 4px;
	}
	/*设置权限弹出框*/
	.dialog_content {
		height: 340px;
		overflow: hidden;
	}
	.dialog_content>div {
		width: 50%;
		height: 340px;
		font-size: 14px;
		color: #545454;
	}
	.dialog_content_right {
		box-sizing: border-box;
		text-align: center;
		border-left: 1px solid #E5E5EA;
	}
	
	.dialog_content_title {
		margin-bottom: 40px;
		text-align: center;
	}
	.dialog_content_main {
		height: 310px;
		overflow-y: scroll;
	}
	.dialog_content_main_list{
		margin-bottom: 20px;
	}
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	/*设置权限弹出框结束*/
</style>


